
<template>
    <div class="index-container">
      <div class="container">
         <div class="content-index">
            <span>幸福</span><br/><span>就是找一个温暖的人</span><br/><span>过一辈子</span>
         </div>
         <div class="heart-container">
           <div class="left-heart"></div>
           <div class="right-heart"></div>
         </div>
      </div>
    </div>
</template>
<script>
import slide from "@/base/slide/slide";

export default {
  data() {
    return {
      items: [
        { imgUrl: require("@/assets/img/love01.png") },
        { imgUrl: require("@/assets/img/love01.png") },
        { imgUrl: require("@/assets/img/love01.png") }
      ],
      popupVisibl: false
    };
  }
};
</script>
<style scoped lang='less'>
.index-container {
  // background: skyblue;
  width: 100%;
  height: 100%;
  // background-image: url('../assets/img/index-bg0.png');
  .container {
    width: 100%;
    height: 100%;
    background-image: url("../assets/img/paperbird.jpg");
    background-repeat: no-repeat no-repeat;
    background-size: 100% 100%;
    filter: grayscale(0.2);
    color: #ff8eb3;
    font-size: 40px;
    word-break: break-all;
    text-indent: 1rem;
    display: flex;
    justify-content: center;
    align-items: stretch;
    h4 {
      width: 100%;
      text-align: center;
      color: #fff;
    }
    .content-index {
      width: 100%;
      height: 200px;
      text-align: center;
      color: #fff;
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -60%);
      z-index: 9;
      span {
        background: linear-gradient(to right, red, blue, yellow);
        -webkit-background-clip: text;
        color: transparent;
      }
    }
    .heart-container {
      width: 350px;
      height: 350px;
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%) rotate(45deg);
      background: #ff8eb3;
      .left-heart {
        background: #ff8eb3;
        width: 350px;
        height: 350px;
        position: absolute;
        transform: rotate(90deg);
        left: -50%;
        border-radius: 50%;
      }
      .right-heart {
        background: #ff8eb3;
        width: 350px;
        height: 350px;
        position: absolute;
        transform: rotate(90deg);
        top: -50%;
        border-radius: 50%;
        z-index: 9;
      }
    }
  }
}
</style>




